@import 'antd/dist/antd.css';
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600&family=Oswald&display=swap');
//********** Variables *******************
$primary-color: #348ccc;
$dark: #001529;
$ash: #ececec;
$smoke: #d6d6d6;
$primary-blue: #1890ff;
$ghee: #f2a365;
$fade: rgb(99, 99, 99);
$light-fade: rgb(94, 94, 94);
$pink: #f9d9eb;
$dark-fade: #494949;
$light-sky: #d5f2fd;
$white: #fff;
$light-pink: #eebed3;
$deep-sky: #10a3ff;
$light-green: #7fd6c2;
$pesta: #ccedd2;
$blue: #5222d0;
$light-blue: #aac6fc;

//************ Mixins ***********
@mixin perfectFit {
  background-size: cover;
  background-position: center;
}
@mixin flex-between {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}
@mixin flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
@mixin align-center {
  display: flex;
  align-items: center;
}
@mixin ash-gradient {
  background-color: #cddcdc;
  background-image: radial-gradient(
      at 50% 100%,
      rgba(255, 255, 255, 0.5) 0%,
      rgba(0, 0, 0, 0.5) 100%
    ),
    linear-gradient(
      to bottom,
      rgba(255, 255, 255, 0.25) 0%,
      rgba(0, 0, 0, 0.25) 100%
    );
  background-blend-mode: screen, overlay;
}
@mixin scrollBarStyle {
  padding-right: 0.3em;
  &::-webkit-scrollbar {
    width: 5px;
  }
  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    border-radius: 10px;
  }
  &::-webkit-scrollbar-thumb {
    background: $primary-color;
    border-radius: 10px;
  }
  &::-webkit-scrollbar-thumb:hover {
    background: $primary-color;
  }
}
//******** Clearcance *********
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}
//********** antd clearance *************
.ant-layout-sider-trigger {
  background-image: linear-gradient(
    to top,
    #1e3c72 0%,
    #1e3c72 1%,
    #2a5298 100%
  );
}
//********** presets *************
button {
  background-color: transparent;
  border-radius: 25px;
  border: none;
  padding: 0.3em 1em;
  transition: all 0.5s ease;
  cursor: pointer;
}
a {
  color: inherit;
  &:hover {
    color: inherit;
    text-decoration: none;
  }
}
//************************* Common Component Styles **************

//modal-container
.modal-container {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  backdrop-filter: blur(3px);
  @include flex-center();
  z-index: 99999;
  .ant-spin-dot-item {
    background-color: #fff;
  }
  .edit-modal {
    background-color: #fff;
    padding: 1em;
    width: 800px;
    h4 {
      padding-bottom: 0.5em;
      border-bottom: 2px solid $light-green;
      margin-bottom: 1em;
      color: $fade;
      @include align-center();
      position: relative;
    }
    .close-btn {
      position: absolute;
      top: 50%;
      right: 2%;
      transform: translateY(-50%);
      font-size: 0.5em;
      height: 25px;
      width: 25px;
      border-radius: 50%;
      @include flex-center();
      background-color: $smoke;
    }
  }
}
//greeting-bar
.greeting-bar {
  background: #667db6; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #667db6,
    #0082c8,
    #0082c8,
    #667db6
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #667db6,
    #0082c8,
    #0082c8,
    #667db6
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  @include flex-between();
  padding: 0.5em 1em;
  margin-bottom: 1em;
  h4 {
    color: $ash;
    margin: 0;
  }
  .date-time {
    font-family: 'Oswald', sans-serif;
  }
  button {
    padding: 0.3em 1em;
    background-color: #fff;
    color: #333;
    border: none;
    font-weight: 500;
  }
}
//blog cards
.blog-container {
  .blog-card-two {
    margin: 0.5em 0;
    padding: 0.5em;
    background: linear-gradient(145deg, #ebebeb, #ffffff);
    height: auto;
    border: 1px solid rgb(212, 212, 212);
    .card-top {
      height: 150px;
      background-size: cover;
      background-position: center;
      position: relative;
      span {
        background-color: #1687a7;
        color: #fff;
        border-radius: 25px;
        min-width: 80px;
        text-align: center;
        position: absolute;
        padding: 0.2em 0.5em;
        top: 5%;
        right: 2%;
      }
    }
    .card-content {
      padding: 2em 0.5em 1em 0.5em;
      position: relative;
      .dp {
        height: 60px;
        width: 60px;
        border: 3px solid #fff;
        border-radius: 50%;
        background-size: cover;
        background-position: center;
        position: absolute;
        top: -15%;
      }
      h5 {
        font-family: 'Montserrat', sans-serif;
        font-weight: 600;
        font-size: 1.2em;
        margin: 0;
        color: #1687a7;
      }
      .c-body {
        min-height: 110px;
      }
      .content {
        * {
          color: $light-fade;
          font-size: 1em;
        }
        max-height: 70px;
        overflow: hidden;
        margin: 0;
      }
      .link {
        color: $deep-sky;
      }
      .author {
        opacity: 0.5;
      }
    }
    .info {
      @include flex-between();
      span {
        @include align-center();
        color: $light-fade;
      }
    }
    .card-bottom {
      display: flex;
      justify-content: space-between;
      margin-top: 1em;
      span {
        @include align-center();
        color: $light-fade;
      }
      button {
        padding: 0 0.5em;
        border: 1px solid #1687a7;
        color: #1687a7;
        &:hover {
          background-color: #1687a7;
          color: #fff;
        }
      }
    }
  }
}

//--------video card
.youtube-video-container {
  overflow: hidden;
  height: 160px;
  position: relative;
  margin-bottom: 1em;
  iframe {
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    position: absolute;
  }
}
.video-cards {
  width: 100%;
  min-height: 350px;
  padding: 1em;
  margin: 0.5em auto;
  background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
  border: 1px solid $smoke;
  border-radius: 17px;
  position: relative;
  .card-info {
    .name {
      display: flex;
      align-items: center;
      margin-bottom: 0.5em;
      h6 {
        margin: 0 0 0 0.5em;
      }
    }
    h5 {
      font-size: 1.2em;
    }
    button {
      width: 100%;
      height: 100%;
      background: #2193b0;
      color: #fff;
      @include flex-center();
      &.delete {
        background: transparent;
        border: 1px solid #ff416c;
        color: #ff416c;
        &:hover {
          background: #ff416c;
          background: -webkit-linear-gradient(to right, #ff4b2b, #ff416c);
          background: linear-gradient(to right, #ff4b2b, #ff416c);
          color: #fff;
        }
      }
    }
  }
}
//*************************** Common Stylings *******************************
.page {
  background-color: #f3f3f3;
  padding: 1em;
  section {
    padding: 1em 0;
    margin: 0.5em 0;
    h5 {
      color: $fade;
    }
  }
}
.icon {
  margin: 0 0.3em;
}
//*************************** Individual Stylings *******************************

//-------------------Sider----------------------------
.sider-container {
  background-color: #fff;
  border-right: 1px solid #dddddd;
  aside {
    background-color: #fff;
  }
  .head {
    padding: 1em 0.5em;
    text-align: center;
    position: relative;
    width: 100%;
    background-image: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)),
      url('https://wallpapercave.com/wp/wp3720126.jpg');
    background-size: cover;
    .avatar {
      width: 60%;
      padding-top: 60%;
      height: auto;
      border-radius: 50%;
      background-image: url('https://www.artificial-solutions.com/wp-content/uploads/personality-bot-teneo.png');
      @include perfectFit;
      box-shadow: 10px 10px 25px rgba(0, 0, 0, 0.2);
      margin: 0.5em auto;
    }
    h5,
    h6,
    p {
      margin: 0;
      color: #fff;
    }
  }
}

//----------------------- Home -----------------------------------------
.home {
  background-color: #fafafa;
  .dashboard {
    .review-card {
      position: relative;
      width: 100%;
      background-image: linear-gradient(to top, #4481eb 0%, #04befe 100%);
      display: flex;
      .figure {
        width: 80%;
        height: 100%;
        padding: 1em 0.5em;
        margin: 0;
        text-align: center;
        img {
          width: 60%;
          transition: all 1s ease;
        }
      }
      .content {
        width: 100%;
        height: min-content;
        padding: 1em 1em;
        p,
        h2 {
          margin: 0;
          color: $ash;
        }
        h2 {
          font-size: 2.5em;
          font-weight: 600;
        }
      }
      &:hover {
        .figure {
          img {
            transform: scale(0.9);
          }
        }
      }
    }
    .new-projects {
      background-image: linear-gradient(to top, #c471f5 0%, #fa71cd 100%);
    }
    .new-blogs {
      background-image: linear-gradient(to top, #0ba360 0%, #3cba92 100%);
    }
    .new-visitors {
      background-image: linear-gradient(-60deg, #ff5858 0%, #f09819 100%);
    }
  }
  .info {
    h5 {
      color: $ash;
      padding: 0.2em 0.5em;
    }
    .mentors-info {
      padding: 0.5em;
      h5 {
        background-image: linear-gradient(
          -225deg,
          #65379b 0%,
          #886aea 53%,
          #6457c6 100%
        );
      }
      table {
        width: 100%;
        td,
        th {
          border: 1px solid #ffffff;
          text-align: left;
          padding: 0.3em;
        }
        tr:nth-child(even) {
          @include ash-gradient();
        }
      }
      button {
        margin: 0.5em 0;
        width: 100%;
        padding: 0.3em;
        border: 1px solid #333;
        background-image: linear-gradient(to top, #e6e9f0 0%, #eef1f5 100%);
        color: #333333;
      }
    }
    .user-stats {
      padding: 0.5em;
      height: 100%;
      position: relative;

      h5 {
        background-image: linear-gradient(to right, #ff758c 0%, #ff7eb3 100%);
      }
      .ant-row {
        height: 85%;
      }
      .stats-card {
        width: 100%;
        height: 93%;
        @include ash-gradient();
        @include flex-center();
        text-align: center;
        h2 {
          margin: 0;
          font-weight: 600;
          font-size: 1.8em;
        }
      }
    }
    .quote-panel {
      padding: 0.5em;
      height: 100%;
      h5 {
        background-image: linear-gradient(-20deg, #2b5876 0%, #4e4376 100%);
      }
      .card {
        height: 100%;
        background-color: #fff;
        padding: 1em;
        h4 {
          font-size: 1.2em;
        }
      }
    }
  }
}
.new-blog-page {
  .new-blogs {
    h3 {
      display: inline-block;
      background-color: #ec4646;
      color: #fff;
      font-size: 1.2em;
      padding: 0.2em 1em;
      margin-bottom: 0.5em;
      border-radius: 25px;
    }
  }
  .all-blogs-btn {
    width: 300px;
    margin: auto;
    padding: 0.3em;
    border: 2px solid #007965;
    color: #007965;
    font-size: 1.2em;
    font-weight: 500;
    z-index: 9999;
    transition: all 1s ease;
    &:hover {
      background-color: #007965;
      color: #fff;
    }
  }
}
.all-blog-page {
  .all-blogs {
    h3 {
      display: inline-block;
      background-color: #00af91;
      color: #fff;
      font-size: 1.2em;
      padding: 0.2em 1em;
      margin-bottom: 0.5em;
      border-radius: 25px;
    }
  }
}
.error-page {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  @include flex-center();
  background-color: rebeccapurple;
  z-index: 9;
  position: fixed;
  h1 {
    color: #fff;
  }
}
//profile
.profile-page {
  background-color: #fff;
  //__________common styles__________
  section {
    width: 100%;
    margin: 1em auto;
    .container {
      position: relative;
    }
  }
  h4.section-head {
    margin-bottom: 1em;
    background-color: $pink;
    color: $dark-fade;
    padding: 0.3em;
    text-transform: uppercase;
    font-family: 'Oswald', sans-serif !important;
  }
  //________Individual section stylings________

  //======= Profile Top ========
  .top {
    background-color: $light-sky;
    padding: 1em 1em 0 1em;
    .container {
      display: flex;
      height: 35vh;
      .profile-picture {
        width: min-content;
        @include flex-center();
        margin: 1em 0;
        .dp {
          position: relative;
          height: 230px;
          width: 230px;
          border-radius: 50%;
          background-size: cover;
          background-position: center;
          border: 10px solid $white;
          box-shadow: -10px -10px 15px #c6efff, 10px 10px 15px #c6efff;
          .img-edit {
            position: absolute;
            bottom: 3%;
            right: 3%;
            background-color: $white;
            height: 35px;
            width: 35px;
            border-radius: 50%;
            @include flex-center();
            border: 1px solid $dark-fade;
          }
        }
      }
      .profile-details {
        width: 100%;
        position: relative;
        padding-left: 3em;
        header {
          padding: 1em 0;
          h3 {
            margin-bottom: 0;
          }
          .headline {
            color: $fade;
          }
          .role {
            width: min-content;
            margin: 1em 0;
            span {
              padding: 0.1em 0.5em;
              margin-right: 0.3em;
              background-color: rgb(255, 255, 255);
              border: 1px solid gray;
              opacity: 0.5;
            }
            span.active {
              background-color: $light-pink;
              border: 1px solid $primary-color;
              opacity: 1;
            }
          }
          .bio {
            p {
              margin-bottom: 0.3em;
              display: flex;
              align-items: center;
              color: $light-fade;
            }
          }
        }
        .action-bar {
          width: 98%;
          background-color: $white;
          position: absolute;
          bottom: 0;
          right: 0;
          .bar {
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
            border: 1px solid gray;
            border-bottom: none;
            padding: 0.3em 0;
            h4 {
              margin: 0;
            }
            button {
              width: 100%;
              height: 100%;
              border: none;
              border-radius: 0;
              background-color: $deep-sky;
              color: $white;
              @include flex-center();
            }
          }
          .btn-bar {
            padding: 0;
            border: none;
          }
        }
        .social-media {
          width: 25%;
          display: flex;
          justify-content: space-around;
          position: absolute;
          top: 0;
          right: 0;
          img {
            width: 25px;
            margin: 0 0.2em;
          }
        }
        .fresh-social-media {
          img {
            filter: grayscale(1);
            opacity: 0.5;
          }
        }
        .message {
          border-radius: 0;
          border: none;
          background-color: $white;
          position: absolute;
          top: 50%;
          right: 0;
          width: 170px;
          transform: translateY(-50%);
          @include flex-center();
        }
        .edit-btn {
          top: 35%;
        }
      }
    }
  }
  //=========== Bio and Achievement Section ===========
  .about-section {
    padding: 1em 0;
    .container {
      h4 {
        border-bottom: 3px solid $light-green;
        padding-bottom: 0.5em;
        display: flex;
        align-items: center;
        img {
          margin-right: 0.5em;
        }
      }
      .bio {
        border: 1px solid #cccccc;
        padding: 1em;
        height: 100%;
        position: relative;
        p {
          color: $fade;
        }
      }

      .achievement-col {
        width: 100%;
      }
      .achievements {
        border: 1px solid #cccccc;
        height: 100%;
        width: 100%;
        padding: 0.5em;
        position: relative;
        .achievement {
          display: flex;
          justify-content: space-between;
          align-items: center;
          background-color: #f0f0f0;
          margin-bottom: 0.3em;
          padding: 0 0.2em;
        }
        .more-btn {
          position: absolute;
          bottom: -10%;
          left: 0;
          width: 100%;
          padding: 0.3em;
          @include flex-center();
          background-color: $pesta;
          cursor: pointer;
        }
      }
    }
  }

  //============== Skills Section ==============

  .skills {
    padding-top: 0.5em;
    .container {
      h4.section-head {
        background-color: $pink;
      }
      .skill-container {
        .skill-card {
          background-color: #f0f0f0;
          padding: 1em;
          margin-bottom: 1em;
          .content {
            padding-left: 0.5em;
            .header {
              display: flex;
              justify-content: space-between;
              margin-bottom: 0.5em;
              h5 {
                margin: 0;
                font-size: 1.2em;
              }
              .icon-container {
                display: flex;
                align-items: center;
                .icon {
                  color: #ff6900;
                }
              }
            }
            p {
              height: 40px;
              color: $fade;
            }
          }
        }
      }
    }
  }

  //============= Profile Project Section =============

  .projects {
    border: none;
    background-color: transparent;
    padding: 1em 0;
    .container {
      h4.section-head {
        background-color: $pesta;
      }
      button {
        border-radius: 0;
        border: 1px solid $light-fade;
        margin-top: 1em;
        @include align-center();
        width: 100%;
        opacity: 0.5;
        &:hover {
          opacity: 1;
        }
      }
      .youtube-video-container {
        height: 178px;
      }
      .video-cards {
        margin: 0.3em 0;
        padding: 0.5em;
        box-shadow: none;
        background-color: #f6f6f6;
        border-radius: 0;
        border: 2px solid $smoke;
        h5 {
          margin-bottom: 0.2em;
        }
        .subtitle {
          margin: 0 0 0.5em 0;
          font-weight: 500;
          color: #1a508b;
        }
        button {
          border: 1px solid #1a508b !important;
          color: #1a508b;
          border: none;
          opacity: 1;
          &:hover {
            background-color: #1a508b;
            color: $smoke;
          }
        }
        .button-group {
          margin: 0;
          padding: 0;
          button {
            width: 100%;
            height: 35px;
            @include flex-center();
            margin: 0.2em 0;
          }
        }
      }
    }
  }
  //======== Profile Performance Section ==========
  .performance {
    .container {
      h4 {
        text-align: center;
        background-color: $blue;
        color: $white;
        border-radius: 25px;
        padding: 0.3em;
        width: 30%;
        margin: 0 auto 2em auto;
        box-shadow: 10px 10px 22px 0 rgba(102, 102, 102, 0.37);
      }
      .ant-steps-item-finish
        > .ant-steps-item-container
        > .ant-steps-item-tail::after {
        background-color: $primary-color;
      }
      .ant-steps-item-finish
        .ant-steps-item-icon
        > .ant-steps-icon
        .ant-steps-icon-dot {
        background: $primary-color;
      }
      .ant-steps-item-process
        .ant-steps-item-icon
        > .ant-steps-icon
        .ant-steps-icon-dot {
        background: $primary-color;
      }
    }
  }
  //==========profile blog section==========
  .blogs {
    padding: 0.5em 0;
    .container {
      h4.section-head {
        background-color: $light-blue;
      }
      //blog-card styling is global
    }
  }

  //========= Profile More Achievements ========
  .more-achievement-modal {
    .achievement-bar-container {
      width: 100%;
      .achievement-bar {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: $smoke;
        padding: 0.3em;
        background-color: $primary-color;
        margin-bottom: 1em;
        a {
          @include align-center();
          .icon {
            margin-left: 0.5em;
          }
        }
        * {
          margin: 0;
        }
      }
    }
  }
  //_____________ Profile Global Stylings End ____________
}
.blog-container {
  .ant-col {
    width: 100%;
  }
  .blog-card {
    width: 100%;
    padding: 0.5em;
    background: linear-gradient(145deg, #ebebeb, #ffffff);
    min-height: 380px;
    border: 1px solid rgb(212, 212, 212);
    margin-bottom: 0.5em;
    .card-top {
      height: 150px;
      background-size: cover;
      background-position: center;
      position: relative;
      span {
        background-color: $primary-color;
        color: $white;
        position: absolute;
        padding: 0.2em 0.5em;
      }
      span.about {
        bottom: 0;
        left: 0;
      }
      span.date {
        top: 1%;
        right: 1%;
        border-radius: 50%;
        text-align: center;
        height: 40px;
        width: 40px;
        font-size: 0.7em;
      }
    }
    .card-content {
      padding: 1em 0.5em 0.5em 0.5em;
      .title {
        font-size: 1.2em;
        margin-bottom: 0.2em;
      }
      .sub-heading {
        color: #eb4b35;
      }
      .c-body {
        min-height: 120px;
      }
      .content {
        * {
          color: rgb(104, 104, 104);
          font-size: 1em;
        }
        max-height: 70px;
        overflow: hidden;
        margin: 0;
      }
      .link {
        color: $deep-sky;
      }
    }
    .card-bottom {
      display: flex;
      justify-content: space-between;
      position: relative;
      span {
        @include align-center();
        color: $light-fade;
      }
      button {
        padding: 0 0.5em;
        border: 1px solid $primary-color;
        color: $primary-color;
        &:hover {
          background-color: $primary-color;
          color: $smoke;
          span {
            color: #fff;
          }
        }
      }
      .blog-edit-btn {
        height: 100%;
        margin-left: 0.3em;
        span {
          color: #eb4b35;
        }
      }
    }
  }
}
//------------------- Full Blog Page Stylings -----------------------------
.blog-details {
  padding: 2em 0;
  .head {
    background-image: linear-gradient(to top, #0ba360 0%, #3cba92 100%);
    padding: 0.2em 1em;
    margin-bottom: 1em;
    h3 {
      color: #fff;
      font-family: 'Oswald', sans-serif;
      margin: 0;
      @include align-center();
      img {
        width: 32px;
        margin-right: 0.5em;
      }
    }
  }
  img {
    width: 100%;
  }
  header {
    padding: 1em 0;
    text-align: center;
    .profile-img {
      height: 80px;
      width: 80px;
      border-radius: 50%;
      margin: 0 auto 1em auto;
      background-image: url('../assets/no-profile-img.jpg');
      background-size: cover;
      background-position: center;
    }
    h4 {
      margin-bottom: 0.3em;
    }
    p {
      margin-bottom: 0.2em;
    }
  }
  hr {
    width: 80%;
    margin: auto;
    opacity: 0.5;
  }
  .blog-content {
    ul,
    ol {
      li {
        list-style-position: inside;
      }
    }
    .heading {
      text-align: center;
      margin: 2em 0;
    }
    h1 {
      color: $fade;
      margin-bottom: 0.3em;
    }
    h5 {
      color: #0032d8ad;
      font-size: 1.5em;
    }
    .content {
      * {
        font-size: 1.2em;
      }
      h1 {
        font-size: 2.3em;
      }
      h2 {
        font-size: 2em;
      }
      h3 {
        font-size: 1.7em;
      }
      h4 {
        font-size: 1.5em;
      }
      h5 {
        font-size: 1.2em;
      }
      h1,
      h2,
      h3,
      h4,
      h5,
      h6 {
        color: $dark-fade;
      }
      p {
        color: $fade;
      }
      line-height: 1.6;
      text-align: justify;
      margin: 1em auto 2em auto;
    }
  }
}
//------------------- Full Project Page Stylings -----------------------------
.project-details {
  .head {
    background-image: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  }
  header {
    background-image: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
