}
//alumni card design
.people-card {
  background-color: $smoke;
  padding: 0.5em;
  margin-bottom: 1em;
  .cover {
    height: 130px;
    background-color: #2586af;
  }
  .people-card-body {
    position: relative;
    padding: 4em 0.5em 1em 0.5em;
    text-align: center;
    .profile-picture {
      height: 95px;
      width: 95px;
      border-radius: 50%;
      margin: auto;
      border: 5px solid $smoke;
      background-image: url('../assets/no-profile-img.jpg');
      background-size: cover;
      background-position: center;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      top: -18%;
    }
    .profile-picture#ashutosh {
      background-image: url('../assets/no-profile-img.jpg');
    }
    .profile-picture#amarjeet {
      background-image: url('../assets/no-profile-img.jpg');
    }
    h4 {
      margin-bottom: 0;
      font-weight: 500;
    }
    .about {
      color: $fade;
      margin-bottom: 1.5em;
    }
    .sm-container {
      display: flex;
      justify-content: center;
      img {
        width: 25px;
        margin: 0 1em;
      }
    }
    button {
      background-color: transparent;
      border: 1px solid#2586af;
      color: #2586af;
      margin-top: 1.5em;
      width: 200px;
      &:hover {
        background-color: #2586af;
        color: $white;
      }
    }
  }
}
.people-card.alumni-card {
  height: 100%;
  .cover {
    background-color: #008891;
  }
  .profile-picture {
    top: -25%;
  }
  h5 {
    font-size: 1.5em;
    margin-bottom: 0;
  }
  hr {
    margin: 1em 0.2em;
    opacity: 0.5;
  }
  .people-card-body {
    padding-top: 3em;
  }
  .people-card-body {
    button {
      border: 1px solid#008891;
      color: #008891;
      &:hover {
        background-color: #008891;
        color: $white;
      }
    }
  }
}
.manage-alumni {
  h4.year {
    background-color: rgb(224, 223, 223);
    color: #008891;
    width: min-content;
    padding: 0.3em;
    min-width: 400px;
    margin: 1.5em 0 1em 0;
    border-radius: 3px;
  }
  h5.new-batch {
    width: min-content;
    padding: 0.3em;
    min-width: 400px;
    background-color: #008891;
    color: #eef1f5;
    border-radius: 25px;
    font-size: 1.5em;
    text-align: center;
    margin: 2em auto;
    @include flex-center();
  }
  .add-card {
    height: 100%;
    width: 100%;
    border: 3px solid $smoke;
    background-color: rgb(226, 226, 226);
    @include flex-center();
    transition: all 0.5s ease;
    cursor: pointer;
    .icon {
      color: #d6d6d6;
      font-size: 45px;
      transition: all 0.5s ease;
    }
    &:hover {
      border: 3px solid #bbbbbb;
      background-color: rgb(221, 221, 221);
      .icon {
        color: #bbbbbb;
      }
    }
  }
}
